import { NetworkServiceProvider } from './../../providers/network-service/network-service';
import { BasePage } from './../base/base';
import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, MenuController, Content, Scroll } from 'ionic-angular';
import { AppServiceProvider, AppGlobal } from '../../providers/app-service/app-service';

/**
 * Generated class for the AirMonitorPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
let rigThis;
declare const $;
@IonicPage()
@Component({
  selector: 'page-air-monitor',
  templateUrl: 'air-monitor.html',
})
export class AirMonitorPage extends BasePage {
  @ViewChild("airContent") airContent: Content;
  @ViewChild("headerAir") headerAir: Scroll;
  @ViewChild("leftAir") leftAir: Scroll;
  @ViewChild("rightAir") rightAir: Scroll;
  arrDate: string = "hour";
  minuteDate: minuteDate = new minuteDate;
  trees: any;
  treeId: any = '';
  rirData: any = [];
  dataType: any = '2';
  pageNum: any = 0;
  pageSize: any = 30;
  style: style = new style;
  displayFormat: any;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public toastCtrl: ToastController,
    public menuCtrl: MenuController,
    public net: NetworkServiceProvider) {
    super(navCtrl, navParams, toastCtrl)
    rigThis = this;
    this.minuteDate.maxDate = new Date(new Date().getTime() + 8 * 60 * 60 * 1000).toISOString();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AirMonitorPage');
    this.scroll();
    this.time();
    this.getAirData();
    this.getSiteTree();
  }
  query() {
    this.menuCtrl.enable(true, "airMenu");
    this.menuCtrl.toggle("airMenu");
  }
  determine() {
    this.rirData = [];
    this.getAirData();
    this.menuCtrl.enable(true, "airMenu");
    this.menuCtrl.toggle("airMenu");
  }
  time() {
    if (this.arrDate == "hour") {
      this.displayFormat = "YYYY-MM-DD hh";
      this.minuteDate.startDate = new Date(new Date().getTime() + 5 * 60 * 60 * 1000).toISOString();
      this.minuteDate.endDate = new Date(new Date().getTime() + 8 * 60 * 60 * 1000).toISOString();
    } else if (this.arrDate == "day") {
      this.displayFormat = "YYYY-MM-DD";
      this.minuteDate.startDate = new Date(new Date().getTime() - 4 * 16 * 60 * 60 * 1000).toISOString();
      this.minuteDate.endDate = new Date(new Date().getTime() + 8 * 60 * 60 * 1000).toISOString();
    }

  }
  monitor(item){
    item.siteType='2';
    this.navCtrl.push('AirDetailsPage', { site: item, id: '1' });

  }
  scrollStyle() {
    let airContent = $("#airContent").height();
    let airHeader = $("#airHeader").height();
    let airHead = $("#airHead").height();
    let airStationNames = $("#airStationNames").height();
    let airHeight = airContent - airHeader - airHead - 30;
    if (airStationNames < airHeight) {
      this.style.airHeight = airStationNames + 'px';
    } else {
      this.style.airHeight = airContent - airHeader - airHead - 30 + 'px';
    }
  }
  scroll() {
    this.rightAir.addScrollEventListener(function (event: any) {
      rigThis.headerAir._scrollContent.nativeElement.scrollLeft = event.target.scrollLeft;
      rigThis.leftAir._scrollContent.nativeElement.scrollTop = event.target.scrollTop;
    });
  }
  getSiteTree() {
    let param: any = {};
    param.userId = AppServiceProvider.getInstance().userInfo.userId;
    param.code = AppServiceProvider.getInstance().userInfo.code;
    this.net.httpPost(
      AppGlobal.API.getSiteTree,
      param, res => {
        console.log(res);
        this.trees = res;
        this.tree();
      }, error => {
        this.toast(error);
      }
    )
  }
  tree() {
    console.log(this.trees["空气站"])
    $('#jsTreeAir').jstree({
      'core': {
        "themes": {
          "dots": false // no connecting dots between dots
        },
        'data': this.trees["空气站"]
      },
      "plugins": ["search"]
    });
    $('#jsTreeAir').jstree().open_all();
    $('#jsTreeAir').on("changed.jstree", (event, data) => {
      var str = data.node.id;

      if (str.slice(0, 1) != "-") {
        this.treeId = str;
      } else {
        this.treeId = '';
      }
    });
  }
  getAirData() {
    let startDate;
    let endDate;
    if (this.arrDate == "hour") {
      startDate = this.minuteDate.startDate.replace("T", " ").replace("Z", "").substring(0, 13) + ':00:00';
      endDate = this.minuteDate.endDate.replace("T", " ").replace("Z", "").substring(0, 13) + ':00:00';
      this.dataType = '2';
    } else if (this.arrDate == "day") {
      startDate = this.minuteDate.startDate.replace("T", " ").replace("Z", "").substring(0, 10) + ' 00:00:00';
      endDate = this.minuteDate.endDate.replace("T", " ").replace("Z", "").substring(0, 10) + ' 00:00:00';
      this.dataType = '3';
    }
    let param: any = {};
    param.siteId = this.treeId;
    param.startTime = startDate;
    param.endTime = endDate;
    param.dataType = this.dataType;
    param.pageNum = this.pageNum;
    param.pageSize = this.pageSize;
    param.userId = AppServiceProvider.getInstance().userInfo.userId;
    param.code = AppServiceProvider.getInstance().userInfo.code;
    this.net.httpPost(
      AppGlobal.API.getAirData,
      param, res => {
        if (res) {
          for (let rirData of res) {
            this.rirData.push(rirData);
          }
        }

        setTimeout(() => {
          this.scrollStyle();
        }, 10)
      }, error => {
        this.toast(error);
      }
    )
  }
  loadMore() {
    this.pageNum = this.rirData.length;
    this.getAirData();
  }

}
class minuteDate {
  maxDate: string = "";
  startDate: string = "";
  endDate: string = "";
}
class style {
  airStationName: string = '';
  airQueryTime: string = '';
  airPm25: string = '';
  airPm10: string = '';
  airO3oneHour: string = '';
  airNo2: string = '';
  airSo2: string = '';
  airCo: string = '';
  airAqi: string = '';
  airPrimaryEp: string = '';
  airAqType: string = '';
  airAqDegree: string = '';
  airHeight: string = '';
}



